//////////////////////
// general
//////////////////////

// links

a, .alink, .btn-link {
  .user-select-none();
  overflow-wrap: break-word;
  word-wrap: break-word;
}

// disabled

[disabled], .disabled {
  cursor: default;
  pointer-events: none;
}

//////////////////////
// group
//////////////////////

.group {
  .link-none();
  position: relative;
  .display(flex);
  .flex-nowrap();
  // .input-control
  > .input-control {
    // border
    &:first-child {
      border-top-right-radius: 0 !important;
      border-bottom-right-radius: 0 !important;
    }
    &:last-child {
      border-top-left-radius: 0 !important;
      border-bottom-left-radius: 0 !important;
    }
    &:not(:first-child):not(:last-child) {
      border-radius: 0 !important;
    }
  }
}

// .group-inner

.group-inner {
  .display(flex);
  .flex-column();
  .flex-wrap();
  // spacing
  margin-right: -@input-border-width;
  .input-control ~ & {
    margin-right: 0;
    margin-left: -@input-border-width;
  }
  // .btn
  > .btn {
    .flex-auto();
    .text-align(center);
    white-space: nowrap;
    & + .btn {
      margin-top: -@btn-border-width;
    }
  }
  .input-control ~ & {
    > .btn {
      .margin(right, none);
    }
  }
  // border inline
  &:first-child {
    > .btn {
      &@{btn-design} {
        border-top-right-radius: 0 !important;
        border-bottom-right-radius: 0 !important;
      }
    }
  }
  &:last-child {
    > .btn {
      &@{btn-design} {
        border-top-left-radius: 0 !important;
        border-bottom-left-radius: 0 !important;
      }
    }
  }
  &:not(:first-child):not(:last-child) {
    > .btn {
      &@{btn-design} {
        border-radius: 0 !important;
      }
    }
  }
  // border stack
  &:first-child {
    > .btn {
      &:not(:first-child) {
        &@{btn-design} {
          border-bottom-right-radius: 0 !important;
          border-top-right-radius: 0 !important;
          border-top-left-radius: 0 !important;
        }
      }
      &:not(:last-child) {
        &@{btn-design} {
          border-top-right-radius: 0 !important;
          border-bottom-right-radius: 0 !important;
          border-bottom-left-radius: 0 !important;
        }
      }
      &:not(:first-child):not(:last-child) {
        &@{btn-design} {
          border-top-left-radius: 0 !important;
          border-bottom-left-radius: 0 !important;
        }
      }
    }
  }
  &:last-child {
    > .btn {
      &:not(:first-child) {
        &@{btn-design} {
          border-bottom-left-radius: 0 !important;
          border-top-left-radius: 0 !important;
          border-top-right-radius: 0 !important;
        }
      }
      &:not(:last-child) {
        &@{btn-design} {
          border-top-left-radius: 0 !important;
          border-bottom-left-radius: 0 !important;
          border-bottom-right-radius: 0 !important;
        }
      }
      &:not(:first-child):not(:last-child) {
        &@{btn-design} {
          border-top-right-radius: 0 !important;
          border-bottom-right-radius: 0 !important;
        }
      }
    }
  }
}

//////////////////////
// btn
//////////////////////

.btn {
  .user-select-none();
  cursor: pointer;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  z-index: @index-base; // fix ::before negative z-index
  &:hover, &.hover, &:active, &.active {
    z-index: @index-active;
  }
  &.out {
    z-index: @index-active - 1;
  }
  > span {
    display: inline-block;
  }
}

// custom

.btn-close {
  z-index: @index-btn-close;
}

//////////////////////
// drop
//////////////////////

.drop-outer {
  display: inline-block;
  position: relative;
  // z-index
  &.active {
    &, > .drop {
      z-index: @index-drop;
    }
  }
  &.out {
    &, > .drop {
      z-index: @index-drop - 1;
    }
  }
  // content
  > .xt-backdrop, > .drop {
    .display(none);
  }
  &.active, &.out {
    > .xt-backdrop, > .drop {
      .display(block);
    }
  }
}

.drop {
  // styles
  position: absolute;
  > .drop-inner {
    position: relative;
    z-index: @index-base; // fix ::before negative z-index
  }
  // others
  &.out {
    pointer-events: none;
  }
}

// addons

.drop-outer-static {
  position: static;
  > .drop,
  > .drop > .drop-inner {
    width: 100%;
  }
}

// before + after

.drop-before() {
  padding: @drop-space;
  left: auto;
  right: 100%;
}

.drop.drop-before {
  .drop-before() !important;
}

.drop-after() {
  padding: @drop-space;
  left: 100%;
  right: auto;
}

.drop.drop-after {
  .drop-after() !important;
}

// left + right

.drop-left() {
  padding: @drop-space 0;
  left: 0;
  right: auto;
}

.drop.drop-left {
  .drop-left() !important;
}

.drop-right() {
  padding: @drop-space 0;
  left: auto;
  right: 0;
}

.drop.drop-right {
  .drop-right() !important;
}

// bottom + top

.drop-bottom() {
  padding: @drop-space 0;
  top: 100%;
  bottom: auto;
}

.drop.drop-bottom {
  .drop-bottom() !important;
}

.drop-top() {
  padding: @drop-space 0;
  top: auto;
  bottom: 100%;
}

.drop.drop-top {
  .drop-top() !important;
}

// center

.drop-center() {
  // custom property
  --center: true;
  > * {
    --center: initial;
  }
}

.drop.drop-center {
  .drop-center() !important;
}

// middle

.drop-middle() {
  padding-top: 0;
  padding-bottom: 0;
  // custom property
  --middle: true;
  > * {
    --middle: initial;
  }
}

.drop.drop-middle {
  .drop-middle() !important;
}

.drop-middle-bottom() {
  top: auto;
  bottom: 0;
}

.drop.drop-middle-bottom {
  .drop-middle-bottom() !important;
}

.drop-middle-top() {
  top: 0;
  bottom: auto;
}

.drop.drop-middle-top {
  .drop-middle-top() !important;
}

// .drop-disable

.drop-disable {
  position: static;
  z-index: initial;
  top: auto;
  left: auto;
  bottom: auto;
  right: auto;
  > a, > button {
    .display(none) !important;
  }
  > .drop {
    .display(block) !important;
    position: relative;
    > .drop-inner {
      z-index: initial;
      min-width: auto;
      &@{drop-design} {
        display: none;
      }
    }
  }
}

//////////////////////
// overlay
//////////////////////

.overlay-outer {
  .toggle-flex();
  position: fixed;
  z-index: @index-overlay;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  // scroll
  overflow-y: scroll !important;
  overflow-x: hidden !important;
  .overflow-style();
}

.overlay {
  .display(flex);
  .flex-column();
  .flex-nowrap();
  .justify-content-center();
  margin: auto; // @FIX http://stackoverflow.com/questions/33454533/cant-scroll-to-top-of-flex-item-that-is-overflowing-container
  width: 100%;
  min-height: 100vh;
}

.overlay-inner {
  position: relative;
  z-index: @index-overlay;
  width: 100%;
  .align-self-center();
}

// addons

.overlay-screen {
  .overlay {
    position: relative;
    z-index: @index-overlay;
    max-width: none !important;
    .padding(all, none) !important;
    .overlay-inner {
      position: static;
    }
  }
  > .xt-backdrop {
    .no-anim() !important;
    opacity: 0 !important;
  }
}

.overlay-pos {
  .overlay {
    position: relative;
    z-index: @index-overlay;
    .justify-content-start();
    .padding(all, none) !important;
    .overlay-inner {
      position: static;
    }
  }
  .overlay-left& {
    .overlay {
      margin-left: 0;
      margin-right: auto;
    }
  }
  .overlay-right& {
    .overlay {
      margin-left: auto;
      margin-right: 0;
    }
  }
}

//////////////////////
// preload
//////////////////////

.preload-container {
  position: absolute;
  z-index: @index-loader;
  top: 0;
  left: 0;
}

.preload {
  position: relative;
  width: 100%;
}

// addons

.preload-container {
  width: 100%;
}

.preload-full {
  .display(flex);
  .align-items-center();
  .justify-content-center();
  height: 100%;
}

.preload-relative {
  position: relative;
}

.preload-fixed {
  position: fixed;
}

.preload-html {
  html.preloaded & {
    display: none;
  }
}

//////////////////////
// plugins
//////////////////////

// .xt-backdrop

.xt-backdrop {
  position: fixed;
  z-index: @index-backdrop;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.over-backdrop {
  &.active, &.out {
    z-index: @index-backdrop + 1;
  }
}

